import { createApp } from 'vue'
import { container, inject, injectable } from '../../domain/core/diContainer'
import { IPresentation } from '../../domain/presentation/IPresentation'
import App from './app.vue';
import { ILogger } from '../../domain/infrastructure/ILogger';
import { IRouter } from '../../domain/presentation/IRouter';
import { RouterImpl } from "./router/routerImpl";
import { IViewModelProxifier } from '../../domain/presentation/IViewModelProxifier';
import { IUseCaseExecutor } from '../../domain/useCase/IUseCaseExecutor';
import { INotification } from '../../domain/presentation/INotification';
import { NotificationImpl } from './impls/notificationImpl';
import { IAsyncComponentCreator } from '../../domain/presentation/IAsyncComponentCreator';
import { AsyncComponentCreator } from './impls/asyncComponentCreator';
import { Vue3Proxifier } from './impls/vue3Proxifier';
import { IDialog } from '../../domain/presentation/IDialog';
import { DialogImpl } from './impls/dialogImpl';
import { ILoading } from '../../domain/presentation/ILoading';
import { LoadingImpl } from './impls/loadingImpl';
import Toast from "vue-toastification";


container.register<IViewModelProxifier>(IViewModelProxifier, Vue3Proxifier);
container.register<INotification>(INotification, NotificationImpl);
container.register<IAsyncComponentCreator>(IAsyncComponentCreator, AsyncComponentCreator);
container.registerInstance<ILoading>(ILoading, new LoadingImpl());

@injectable()
export class Vue3Presentation implements IPresentation {
    constructor(
        @inject(ILogger) private readonly logger: ILogger,
        @inject(IUseCaseExecutor) private readonly executor: IUseCaseExecutor,
        @inject(IAsyncComponentCreator) private readonly compCreator: IAsyncComponentCreator,
    ) { }
    start(): void {
        this.logger.log({ message: "Vue 3 application starting...", level: "log" });

        const router = new RouterImpl(this.executor, this.logger);
        container.registerInstance<IRouter>(IRouter, router);
        container.registerInstance<IDialog>(IDialog, new DialogImpl());

        const app = this.compCreator.instantiateComponent(App, undefined, undefined) as ReturnType<typeof createApp>;
        app.use(router.getInternalRouter());
        app.use(Toast);
        app.mount('#app');

        this.logger.log({ message: "Vue 3 application started", level: "log" });
    }
}