import { POSITION, TYPE, useToast } from "vue-toastification";
import { ToastOptions } from "vue-toastification/dist/types/types";
import { createPromiseData } from "../../../common/promiseHelper";
import { toUpperCase } from "../../../common/stringType";
import { inject, injectable } from "../../../domain/core/diContainer";
import { IConfirmOptions, IConfirmRetval, INotificationInteractor, INotificationOptions } from "../../../domain/model/INotificationOptions";
import { ILoading } from "../../../domain/presentation/ILoading";
import { INotification } from "../../../domain/presentation/INotification";

const toast = useToast();

@injectable()
export class NotificationImpl implements INotification {
    constructor(
        @inject(ILoading) private readonly loading: ILoading,
    ) { }

    public async showNotification(options: INotificationOptions) {
        const type = TYPE[toUpperCase(options.type)];
        let opt: ToastOptions = { type, closeOnClick: true, timeout: options.timeout, pauseOnFocusLoss: options.pauseOnFocusLoss, pauseOnHover: options.pauseOnHover };
        if (options.interactors?.find(interactor => interactor.required)) {
            opt = { closeButton: false, closeOnClick: false, draggable: false, timeout: options.timeout || false, position: POSITION.TOP_CENTER, type, pauseOnFocusLoss: options.pauseOnFocusLoss, pauseOnHover: options.pauseOnHover };
        }
        toast({ component: (await import("../components/notification.vue")).default, props: { options } }, opt);
    }
    public async showConfirm(options: IConfirmOptions) {
        const prom = createPromiseData<IConfirmRetval>();
        const notifOptions: INotificationOptions = options;
        const intOptions: INotificationInteractor["options"] = [{ id: "approve", text: options.approveText }];
        if (options.cancelText) {
            intOptions.push({ id: "cancel", text: options.cancelText });
        }
        if (options.rejectText) {
            intOptions.push({ id: "reject", text: options.rejectText });
        }
        notifOptions.interactors = [{
            required: true, options: intOptions, optionsType: "button",
            onInteract: option => prom.resolver({ approved: option.selectedOption!.id == "approve", result: option.selectedOption!.id as IConfirmRetval["result"] }),
        }];

        this.loading.stash(async () => { this.showNotification(notifOptions); await prom.promise; });

        return prom.promise;
    }
}