onst containerStyle: StyleValue = { zIndex: props.zindex };\nconst bkockShadowStyle: StyleValue = { zIndex: props.zindex };\nlet animating: NodeJS.Timeout | undefined;\nlet appearStart: number = Date.now();\nwatch(props.appear, (val) => {\n    if (animating) { clearTimeout(animating); }\n    if (val) {\n        appear.value = val;\n        animating = setTimeout(() => appearAnimate.value = val);\n        appearStart = Date.now();\n    } else {\n        appearAnimate.value = val;\n        let animationLeft = Date.now() - appearStart;\n        animationLeft = animationLeft > 1000 ? 1000 : animationLeft; // 1000 -> .blockShadow { transition: time }\n        animating = setTimeout(() => appear.value = val, animationLeft);\n    }\n});\nonMounted(() => { appear.value = false; });\n\n</script>\n<template>\n    <teleport to=\"body\">\n        <div class=\"blockShadow\" v-show=\"appear\" :class=\"computedClass\" :style=\"bkockShadowStyle\"></div>\n    </teleport>\n    <div class=\"container\" v-show=\"appear\" :class=\"computedClass\" :style=\"containerStyle\">\n\n        <div class=\"row position-absolute top-50 start-50 translate-middle\">\n        <div class=\"col\">\n            <div class=\"logo\" style=\"display: flex; align-items: center;\">\n                <img src=\"../../../../images/loadingLogo.png\" alt=\"ide5-logo\" width=\"80\" height=\"74\">\n                <p class=\"studio-text mt-2\">Studio</p>\n            </div>\n            <div class=\"cogs\">\n                <div class=\" position-relative\">\n                    <i id=\"gear1\" class=\"mdi mdi-cog spin\"></i>\n                    <i id=\"gear2\" class=\"mdi mdi-cog spin-back\"></i>\n                    <i id=\"gear3\" class=\"mdi mdi-cog spin\"></i>\n                </div>\n            </div>\n            <div>\n                <p class=\"loading-text ms-3\">building blocks...</p>\n            </div>\n        </div>\n    </div>\n\n<!-- \n        <div class=\"loader-wrapper\">\n          <img src=\"../../../../images/ide5-logo.PNG\" alt=\"ide5-logo\" width=\"71px\" height=\"74px\">\n                          <div class=\"title-container\">\n            <p class=\"loading-title\">Studio</p>\n          </div>\n            <div><span class=\"loader\"><span class=\"loader-inner\"></span></span></div>\n        </div> -->\n    </div>\n</template>\n<style scoped>\n#gear1, #gear2, #gear3 {\n    color: white;\n    display: block;\n    float: left;\n    position: absolute;\n}\n            \n#gear1 {\n    top: 134px;\n    left: 40px;\n    font-size: 56px;\n    color: white;\n}\n            \n#gear2 {\n    left: 60px;\n    font-size: 86px;\n    color: white;\n}\n\n#gear3 {\n    top: 45px;\n    left: 110px;\n    font-size: 136px;\n    color: white;\n}\n\n.loading-text{\n    color: white;\n    font-size: 36px;\n    font-weight: 500;\n}\n\n.studio-text {\n    font-size: 63px; \n    letter-spacing: 1.57px; \n    color: white; \n    font-weight: 400; \n    font-family: sans-serif;\n}\n\n.cogs {\n    height: 270px;\n}\n\n.spin {\n    -webkit-animation:spin 4s linear infinite;\n    -moz-animation:spin 4s linear infinite;\n    animation:spin 4s linear infinite;\n}\n\n.spin-back {\n    -webkit-animation:spin-back 4s linear infinite;\n    -moz-animation:spin-back 4s linear infinite;\n    animation:spin-back 4s linear infinite;\n}\n\n@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }\n@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }\n@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }\n\n@-moz-keyframes spin-back { 100% { -moz-transform: rotate(-360deg); } }\n@-webkit-keyframes spin-back { 100% { -webkit-transform: rotate(-360deg); } }\n@keyframes spin-back { 100% { -webkit-transform: rotate(-360deg); transform:rotate(-360deg); } }\n.blockShadow {\n    background-color: black;\n    top: 0px;\n    left: 0px;\n    position: fixed;\n    height: 100%;\n    width: 100%;\n    opacity: 0;\n    transition: opacity 1s ease-in-out 0.01s;\n}\n\n.blockShadow.appearAnimate {\n    opacity: 0.8;\n}\n.loading-title{\n        margin-left: 10px;\n    color: white;\n    font-weight: 700;\n    font-size: 40px;\n\n\ntext-align: left;\nletter-spacing: 1.57px;\n\nopacity: 1;\n}\n\n.container {\n    position: fixed;\n    left: 0%;\n    top: 50%;\n    transition: left 0.5s ease-in-out, opacity 0.5s ease-in-out;\n    opacity: 0;\n    background-color: red;\n}\n\n.container.appearAnimate {\n    left: 20%;\n    opacity: 1;\n}\n\nbody {\n    margin: 0;\n    padding: 0;\n    width: 100vw;\n    height: 100vh;\n    background-color: #eee;\n}\n\n.content {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n}\n\n.loader-wrapper {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    left: 0;\n    background-color: #242f3f;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.loader {\n    display: inline-block;\n    width: 30px;\n    height: 30px;\n    position: relative;\n    border: 4px solid #Fff;\n    animation: loader 2s infinite ease;\n}\n\n.loader-inner {\n    vertical-align: top;\n    display: inline-block;\n    width: 100%;\n    background-color: #fff;\n    animation: loader-inner 2s infinite ease-in;\n}\n\n@keyframes loader {\n    0% {\n        transform: rotate(0deg);\n    }\n\n    25% {\n        transform: rotate(180deg);\n    }\n\n    50% {\n        transform: rotate(180deg);\n    }\n\n    75% {\n        transform: rotate(360deg);\n    }\n\n    100% {\n        transform: rotate(360deg);\n    }\n}\n\n@keyframes loader-inner {\n    0% {\n        height: 0%;\n    }\n\n    25% {\n        height: 0%;\n    }\n\n    50% {\n        height: 100%;\n    }\n\n    75% {\n        height: 100%;\n    }\n\n    100% {\n        height: 0%;\n    }\n}\n</style>"],"sourceRoot":""}]);
