import { createApp, CreateAppFunction, defineAsyncComponent, markRaw } from "vue";
import { IAsyncComponentCreator } from "../../../domain/presentation/IAsyncComponentCreator";
import { injectable } from "../../../domain/core/diContainer";
import { ClientTable } from 'v-tables-3';

@injectable()
export class AsyncComponentCreator implements IAsyncComponentCreator {
    public instantiateComponent(asyncComponent: object, props: Record<string, unknown> | undefined, mountPoint: HTMLElement | undefined): object {
        const inst = createApp(asyncComponent, props);

        inst.use(ClientTable);

        if (mountPoint) {
            inst.mount(mountPoint);
        }

        return inst;
    }
    public destroyComponent(componentInstance: object) {
        if (!componentInstance) { return; }

        (componentInstance as ReturnType<CreateAppFunction<Element>>).unmount();
    }


    public createSidebarComponent(componentPath: string) {
        return markRaw(defineAsyncComponent(() => import(`../components/sidebars/${componentPath}`)));
    }
    public createLoginDialogComponent() {
        return defineAsyncComponent(() => import(`../components/dialogs/internalLogin.vue`));
    }
    public createNewItemComponent() {
        return defineAsyncComponent(() => import(`../components/dialogs/newItem.vue`));
    }
    public createModelHistoriesComponent() {
        return defineAsyncComponent(() => import(`../components/dialogs/listModelHistories.vue`));
    }
    public createEditModuleComponent() {
        return defineAsyncComponent(() => import(`../components/dialogs/editModule.vue`));
    }
    public createProcessEditor() {
        return defineAsyncComponent(() => import("../editors/processEditor/processEditor.vue"));
    }
    public createProcessDesignEditor() {
        return defineAsyncComponent(() => import("../editors/processDesignEditor.vue"));
    }
}