import { isRef, markRaw, ref } from "vue";
import { injectable } from "../../../domain/core/diContainer";
import { IViewModelProxifier } from "../../../domain/presentation/IViewModelProxifier";

@injectable()
export class Vue3Proxifier implements IViewModelProxifier {
    public isProxified<T extends object>(obj: T) { return isRef(obj); }
    public proxify<T extends object>(obj: T): T { return ref(obj).value; }
    public markRaw<T extends object>(obj: T | undefined): T | undefined { return (obj === undefined || typeof obj === "string") ? obj : markRaw(obj); }
}