import { inject } from "../../../domain/core/diContainer";
import * as VueRouter from "vue-router";
import { IRouter, IRouteDefinition } from "../../../domain/presentation/IRouter";
import { routes } from "./routes";
import { ITimedOut } from "../../../domain/useCase/ITimedOut";
import { IUseCaseExecutor } from "../../../domain/useCase/IUseCaseExecutor";
import { ICheckLogin } from "../../../domain/useCase/ICheckLogin";
import { ILogger } from "../../../domain/infrastructure/ILogger";

interface IRouterMeta extends VueRouter.RouteMeta, IRouteDefinition {
    popstate: boolean;
}
let isPopstate = false;
window.addEventListener('popstate', popEvent => isPopstate = true);
export class RouterImpl implements IRouter {
    private readonly router: VueRouter.Router;
    constructor(
        @inject(IUseCaseExecutor) private readonly executor: IUseCaseExecutor,
        @inject(ILogger) private readonly logger: ILogger,
    ) {
        const url = new URL(window.location.href);
        const hostingOptions = ["dist/client/index.html", "dist/client", "dist", "qcloud-shell", "test-qcloud-shell"];
        const option = hostingOptions.find(option => url.pathname.startsWith("/" + option));

        this.router = VueRouter.createRouter({
            history: VueRouter.createWebHistory(option),
            routes: routes as Array<VueRouter.RouteRecordRaw>,
        });

        this.router.beforeEach((to, from, next) => {
            this.executor.execute(async () => {
                if (!(to.meta as IRouterMeta).noAuth) {
                    if (!await this.executor.executeUseCase<ICheckLogin>(ICheckLogin, { via: "client" })) {
                        this.logger.log({ level: "error", message: `missing authentication for '${to.path}' assuming timeout.` });
                        this.executor.executeUseCase<ITimedOut>(ITimedOut);
                        return;
                    }
                }

                next();
            }, { loading: true });
        });
        this.router.afterEach((to, from, fail) => {
            (to.meta as IRouterMeta).popstate = isPopstate;
            isPopstate = false;
        });
    }

    public async push(to: string): Promise<void> {
        await this.router.push(to);
    }

    public getInternalRouter() {
        return this.router;
    }

    public getRouteParams<T extends VueRouter.RouteParams>() {
        const route = VueRouter.useRoute();
        return route.params as T;
    }
    private getRouteMeta<T extends VueRouter.RouteMeta>() {
        const route = VueRouter.useRoute();
        return route.meta as T;
    }
    public isBackOrForward() {
        return this.getRouteMeta<IRouterMeta>().popstate || false;
    }
}
